.wcf__flip_box {
  display: block;
  height: 300px;
  background-color: transparent;
  perspective: 1000px;

  .wcf__btn{
    margin-top: 30px;
  }

  .flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .flip-box-front,
  .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 10px;
    transform-style: preserve-3d;
    background-color: #f2f1f1;
  }

  .flip-box-back {
    z-index: 2;
  }

  .flip-box-icon-image{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    margin-bottom: 10px;
    line-height: 1;
    img{
      width: 60px;
    }
    i,
    svg {
      width: 1em;
      height: 1em;
      position: relative;
      display: block;
    }
  }

  //3D
  &.flip-box--3d{
    .flip-box-front-inner,
    .flip-box-back-inner{
      transform: translateZ(90px) scale(.91);
    }
  }

  //animation
  &.flip-box-animate-left {
    .flip-box-back {
      transform: rotateY(-180deg);
    }

    &:hover .flip-box-inner {
      transform: rotateY(-180deg);
    }
  }

  &.flip-box-animate-right {
    .flip-box-back {
      transform: rotateY(180deg);
    }

    &:hover .flip-box-inner {
      transform: rotateY(180deg);
    }
  }

  &.flip-box-animate-up {
    .flip-box-back {
      transform: rotateX(180deg);
    }

    &:hover .flip-box-inner {
      transform: rotateX(180deg);
    }
  }

  &.flip-box-animate-down {
    .flip-box-back {
      transform: rotateX(-180deg);
    }

    &:hover .flip-box-inner {
      transform: rotateX(-180deg);
    }
  }

  &.flip-box-animate-zoom-in {
    .flip-box-back {
      opacity: 0;
      transform: scale(0.75);
      transition: all 0.5s;
    }

    &:hover .flip-box-back {
      opacity: 1;
      transform: scale(1);
    }
  }

  &.flip-box-animate-zoom-out {
    .flip-box-front {
      transition: all 0.5s;
      z-index: 3;
    }

    &:hover .flip-box-front {
      opacity: 0;
      visibility: hidden;
      transform: scale(0.75);
    }
  }

  &.flip-box-animate-fade-in {
    .flip-box-back {
      opacity: 0;
      transition: all 0.5s;
    }

    &:hover .flip-box-back {
      opacity: 1;
    }
  }
}
