.wcf__flip_box {
  display: block;
  height: 300px;
  background-color: transparent;
  perspective: 1000px;
}
.wcf__flip_box .wcf__btn {
  margin-top: 30px;
}
.wcf__flip_box .flip-box-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wcf__flip_box .flip-box-front,
.wcf__flip_box .flip-box-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
  transform-style: preserve-3d;
  background-color: #f2f1f1;
}
.wcf__flip_box .flip-box-back {
  z-index: 2;
}
.wcf__flip_box .flip-box-icon-image {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  margin-bottom: 10px;
  line-height: 1;
}
.wcf__flip_box .flip-box-icon-image img {
  width: 60px;
}
.wcf__flip_box .flip-box-icon-image i,
.wcf__flip_box .flip-box-icon-image svg {
  width: 1em;
  height: 1em;
  position: relative;
  display: block;
}
.wcf__flip_box.flip-box--3d .flip-box-front-inner,
.wcf__flip_box.flip-box--3d .flip-box-back-inner {
  transform: translateZ(90px) scale(0.91);
}
.wcf__flip_box.flip-box-animate-left .flip-box-back {
  transform: rotateY(-180deg);
}
.wcf__flip_box.flip-box-animate-left:hover .flip-box-inner {
  transform: rotateY(-180deg);
}
.wcf__flip_box.flip-box-animate-right .flip-box-back {
  transform: rotateY(180deg);
}
.wcf__flip_box.flip-box-animate-right:hover .flip-box-inner {
  transform: rotateY(180deg);
}
.wcf__flip_box.flip-box-animate-up .flip-box-back {
  transform: rotateX(180deg);
}
.wcf__flip_box.flip-box-animate-up:hover .flip-box-inner {
  transform: rotateX(180deg);
}
.wcf__flip_box.flip-box-animate-down .flip-box-back {
  transform: rotateX(-180deg);
}
.wcf__flip_box.flip-box-animate-down:hover .flip-box-inner {
  transform: rotateX(-180deg);
}
.wcf__flip_box.flip-box-animate-zoom-in .flip-box-back {
  opacity: 0;
  transform: scale(0.75);
  transition: all 0.5s;
}
.wcf__flip_box.flip-box-animate-zoom-in:hover .flip-box-back {
  opacity: 1;
  transform: scale(1);
}
.wcf__flip_box.flip-box-animate-zoom-out .flip-box-front {
  transition: all 0.5s;
  z-index: 3;
}
.wcf__flip_box.flip-box-animate-zoom-out:hover .flip-box-front {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.75);
}
.wcf__flip_box.flip-box-animate-fade-in .flip-box-back {
  opacity: 0;
  transition: all 0.5s;
}
.wcf__flip_box.flip-box-animate-fade-in:hover .flip-box-back {
  opacity: 1;
}